/** Utility helpers shared by server and client layers to turn raw
 * database rows into dashboard friendly view models.
 */
export function toISODate(input) {
  if (!input) return null
  const date = input instanceof Date ? input : new Date(String(input))
  return Number.isNaN(date.getTime()) ? null : date.toISOString()
}

export function mapSummaryRow(row) {
  return {
    totalCount: Number(row?.totalCount ?? 0),
    activeCount: Number(row?.activeCount ?? 0),
    completedCount: Number(row?.completedCount ?? 0),
    pendingJiraCount: Number(row?.pendingJiraCount ?? 0),
    lotCount: Number(row?.lotCount ?? 0),
    latestUpdatedAt: toISODate(row?.latestUpdatedAt),
  }
}

export function mapTrendRows(rows) {
  return (rows ?? []).map((row) => ({
    date: (toISODate(row?.day) ?? "").slice(0, 10),
    activeCount: Number(row?.activeCount ?? 0),
    completedCount: Number(row?.completedCount ?? 0),
  }))
}

export function mapRecentRows(rows) {
  return (rows ?? []).map((row) => ({
    id: row?.id ?? null,
    lotId: row?.lot_id ?? null,
    status: row?.status ?? null,
    createdAt: toISODate(row?.created_at) ?? "",
  }))
}
